.logo {
  width: 30%;
  margin-left: 30px;
  margin-right: -100px;
}

#navbar {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  align-items: center;
  padding-top: 25px;
  margin: 0;
  box-shadow: 10px 8px 15px #dadada;
  height: 10vh;
  position: relative;
}

#navbar-logo-skeeelled {
  padding: auto;
  margin: auto;
  height: 10vh;
  width: 15vw;
  vertical-align: baseline;
}

.course-link,
.home-link,
.simulation-link {
  color: #41413f;
  font-size: 20px;
}

.course-link-dark,
.home-link-dark {
  color: white;
  font-size: 20px;
}

.add-question-link {
  margin-left: auto;
  color: #41413f;
  font-size: 20px;
}

.add-question-link-dark {
  margin-left: auto;
  color: white;
  font-size: 20px;
}

.course-link:hover,
.course-link-dark:hover,
.home-link:hover,
.home-link-dark:hover,
.add-question-link:hover,
.add-question-link-dark:hover,
.simulation-link:hover,
.simulation-link-dark:hover {
  color: #00983a;
}

#logout-icon {
  margin: auto;
}

#user-dropdown {
  padding-right: 5%;
}

.dropdown-link {
  text-decoration: none;
}

.dropdown-item {
  padding: 4px 16px 4px 16px;
}
